import { Remove, Add, Delete } from "@mui/icons-material"
import { LoadingButton } from "@mui/lab"
import { TableContainer, Paper, Table, TableHead, TableRow, TableCell, TableBody, Box } from "@mui/material"
import { BasketItem } from "../../App/models/basket"
import { useAppSelector, useAppDispatch } from "../../App/store/configureStore"
import { currencyFormat } from "../../App/util/util"
import { removeBasketItemAsync, addBasketItemAsync } from "./basketSlice"

interface Props {
    items: BasketItem[];
    isBasket?: boolean;
}
const BasketTable = ({items, isBasket = true}: Props) => {
    const { basket, status } = useAppSelector(state => state.basket);
	const dispatch = useAppDispatch();
  return (
        <TableContainer component={Paper}>
				<Table sx={{ minWidth: 650 }} aria-label="simple table">
					<TableHead>
						<TableRow>
							<TableCell>Product</TableCell>
							<TableCell align="right">Price</TableCell>
							<TableCell align="right">Quantity</TableCell>
							<TableCell align="right">SubTotal</TableCell>
                            {isBasket && 
							<TableCell align="right"></TableCell>}
						</TableRow>
					</TableHead>
					<TableBody>
						{items.map((item) => (
							<TableRow
								key={item.productId}
								sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
							>
								<TableCell component="th" scope="row">
									<Box display="flex" alignItems="center">
										<img
											src={item.pictureUrl}
											alt={item.name}
											style={{ height: 50, marginRight: 20 }}
										/>
										<span>{item.name}</span>
									</Box>
								</TableCell>
								<TableCell align="right">
									{currencyFormat(item.price)}
								</TableCell>
								<TableCell align="right">
                                {isBasket && 
									<LoadingButton
										loading={
											status === 'pendingRemoveItem'+item.productId + 'rem'
										}
										onClick={() =>
											dispatch(removeBasketItemAsync({productId: item.productId, quantity: 1, name: 'rem'}))
										}
										color="error"
									>
										<Remove />
									</LoadingButton>}
									{item.quantity}
                                    {isBasket && 
									<LoadingButton
										loading={
											status === 'pendingAddItem' + item.productId
										}
										onClick={() =>
											dispatch(addBasketItemAsync({productId: item.productId}))
										}
										color="secondary"
									>
										<Add />
									</LoadingButton>}
								</TableCell>
								<TableCell align="right">
									{currencyFormat(item.price * item.quantity)}
								</TableCell>
                                {isBasket && 
								<TableCell align="right">
									<LoadingButton
										loading={
											status === 'pendingRemoveItem' + item.productId + 'del'
										}
										onClick={() =>
											dispatch(removeBasketItemAsync({productId: item.productId, quantity: item.quantity, name: 'del'}))
										}
										color="error"
									>
										<Delete />
									</LoadingButton>
								</TableCell>}
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
  )
}

export default BasketTable